import { useNavigate } from 'react-router-dom';
import WebApp from '@twa-dev/sdk';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import CategoryEditor from './CategoryEditor';
import { toast } from 'react-toastify';
import { BackButton, MainButton } from '@twa-dev/sdk/react';
import { Loader, Placeholder } from 'rsuite';

const Menu = () => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    axios
      .get('https://api.shashlichny-dom.ru/api/data')
      .then((response) => {
        setData(response.data);
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
        toast.error('  .');
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  const handleCategoryUpdate = (category, updatedItems) => {
    setData(prevData => ({
      ...prevData,
      [category]: updatedItems,
    }));
  };

  const saveData = () => {
    axios
      .put('https://api.shashlichny-dom.ru/api/save', data)
      .then(() => toast.success('  !'))
      .catch((error) => {
        console.error('Error saving data:', error);
        toast.error('  .');
      });
  };

  if (loading) return (
    <div className='w-full h-full flex justify-center items-center'>
      <Loader size="lg"/>
    </div>  )

  return (
    <div className='w-full relative pb-20'> 
      {data && Object.keys(data).map(category => (
        <CategoryEditor
          key={category}
          category={category}
          items={data[category]}
          onUpdate={(updatedItems) => handleCategoryUpdate(category, updatedItems)}
        />
      ))}
      <BackButton onClick={() => navigate('/admin-shd')} />
      <MainButton onClick={saveData} />
    </div>
  );
};

export default Menu;